import React from "react";
import "../css/Navigation.scss";
import "../fonts/DelaGothicOne-Regular.ttf";
import "../fonts/Montserrat-Medium.ttf";
import { Link, withRouter } from "react-router-dom";

class Navigation extends React.Component {

  componentDidMount() {
    if(this.props.location.pathname === "/") {
      document.getElementById('body').style.background = "linear-gradient(180deg, #000000 15%, #FFFFFF)";
    }
    else if(this.props.location.pathname === "/projects") {
      document.getElementById('body').style.background = "#FFFFFF";
    }
    else if(this.props.location.pathname === "/about-me") {
      document.getElementById('body').style.background = "#FFFFFF";
    }
    console.log(this.props.location);
  }

  render() {
    return(
      <nav id="nav-bar" className="text-center">
        <div id="links-section" className="row">
          <div className="col-sm-4 col-xs-4">
            <Link id="welcome-link" to="/" className="nav-links">Welcome</Link>
          </div>
          <div className="col-sm-4 col-xs-4">
            <Link id="projects-link" to="/projects" className="nav-links" onClick={() => document.getElementById('body').style.background = "#FFFFFF"}>Projects</Link>
          </div>
          <div className="col-sm-4 col-xs-4">
            <Link id="about-me-link" to="/about-me" className="nav-links" onClick={() => document.getElementById('body').style.background = "#FFFFFF"}>About Me</Link>
          </div>
        </div>
      </nav>
    );
  }
};

export default withRouter(Navigation);
