import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { useLocation } from "react-router-dom";
import '../css/App.css';
import Navigation from "./Navigation";
import Section from "./Section";
import Education from "./Education";
import Technology from "./Technology";
import ContactMe from "./ContactMe";
import CalculatorProject from "./CalculatorProject";
import ClockProject from "./ClockProject";
import DrumMachineProject from "./DrumMachineProject";
import MarkdownPreviewer from "./MarkdownPreviewer";
import RandomQuoteGenerator from "./RandomQuoteGenerator";
import SurveyForm from "./SurveyForm";
import Welcome from "./Welcome";
import "../fonts/Ubuntu-Light.ttf";

class App extends React.Component {

  render() {
    return(
      <div>
        <Router>
          <Navigation />
          <Switch>
            <Route path="/" exact component={() => <Welcome />} />
            <Route path="/projects" exact component={() =>
              <Section id="project-section">
                <h1 id="project-title">Here are some projects I have completed recently:</h1>
                <CalculatorProject />
                <ClockProject />
                <DrumMachineProject />
                <MarkdownPreviewer />
                <RandomQuoteGenerator />
                <SurveyForm />
              </Section>} />

            <Route path="/about-me" exact component={() =>
              <ContactMe />
              } />
          </Switch>
        </Router>
      </div>
    );
  }
};

export default App;
